@import url('https://fonts.googleapis.com/css2?family=Baskerville:wght@700&display=swap');

.video-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    z-index: -1;
}

.fullscreen-video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    transform: translate(-50%, -50%);
}

.pre-screen {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black; 
    z-index: 1; 
}

.background-image {
    position: absolute; 
    top: 0; 
    width: 100%; 
    height: 100%; 
    object-fit: cover; 
    z-index: 0;
}


.start-button {
    position: absolute;
   
    bottom: 15vh; 
    transform: translateX(-50%); 
    z-index: 2; 
    animation: pulse 2s infinite; 
}

.start-button img {
    width: 300px; 
    height: auto; 
}

@keyframes pulse {
    0% {
        transform: scale(1); 
        opacity: 1; 
    }
    50% {
        transform: scale(1.1); 
        opacity: 0.8; 
    }
    100% {
        transform: scale(1);
        opacity: 1; 
    }
}
